*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  width:100%;
  font-size: 1.2rem;
  color:grey;
}

.card{
  align-content: center;
  border: solid 1px grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right:10px ;
  overflow:hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-wrap: wrap;
}